export default {
    data() {
        return {
            mode: {
                sign: {
                    plus: true,
                    minus: true,
                },
                side: {
                    right: true,
                    left: false,
                },
                maxNumber: 20,
            },
            history: {
                lengthMax: 100,
                length: null,
                list: {},
            },
            round: {
                sign: null,
                side: null,
                input: null,
                number: {
                    sum: null,
                    term1: null,
                    term2: null,
                },
                equation: {},
            },
            display: {
                sign: {
                    plus: '+',
                    minus: '-',
                }
            },
            assertResult: null,
            resultFlag: null,
            inputFlag: false,
            resultList: [],
            inputList: [],
        }
    },
    mounted() {
        // this.gameNextLevel();
    },
    watch: {
        resultFlag() {
            if (this.resultFlag === true) {
                this.hintFlag = true
            }
        }
    },
    methods: {
        gameRefreshLevel() {
            // this.inputList = this.buildInputList();
            // this.resultList = this.buildResultList();
        },
        gameNextLevel() {
            // this.assertResult = this.buildAssertResult();
            // this.gameRefreshLevel();
        },
        prepareHistory() {
            let historyLength = 0;
            for (let i = 0; i <= this.mode.maxNumber; i++) {
                historyLength += Math.floor(i/2)+1
                if (historyLength >= this.history.lengthMax) {
                    historyLength = this.history.lengthMax;
                    break;
                }
            }
            this.history.length = historyLength;
            // prepare history list
            Object.keys(this.mode.sign).forEach(e => {this.history.list[e] = []});
        },
        historyCheck(term1, term2) {
            return this.history.list[this.round.sign].indexOf([
                term1 + term2,
                (term1 > term2) ? term1 : term2
            ].join()) === -1
        },
        historyAdd(term1, term2) {
            if (this.history.list[this.round.sign].length + 1 >= this.history.length) {
                this.history.list[this.round.sign] = []
            }
            this.history.list[this.round.sign].push([
                term1 + term2,
                (term1 > term2) ? term1 : term2
            ].join())
        },
        buildSign() {
            this.round.sign = Object.entries(this.mode.sign)
            .filter(e => !!e[1])
            .map(e => e[0])
            .sort(() => 0.5 - Math.random())[0]
        },
        buildSide() {
            this.round.side = Object.entries(this.mode.side)
            .filter(e => !!e[1])
            .map(e => e[0])
            .sort(() => 0.5 - Math.random())[0]
        },
        buildNumbers() {
            let term1, term2, flagDoing = true;
            while(flagDoing) {
                term1 = Math.floor(Math.random() * (this.mode.maxNumber + 1))
                term2 = Math.floor(Math.random() * (this.mode.maxNumber + 1))
                if((term1 + term2) > this.mode.maxNumber) continue
                if(!this.historyCheck(term1, term2)) continue
                flagDoing = false;
            } 
            this.historyAdd(term1, term2)
            this.round.number.term1 = term1
            this.round.number.term2 = term2
            this.round.number.sum = term1 + term2
        },
        
    },
}