export default {
    methods: {
        digitCreate(value, disabled = false) {
            return {
                id: value,
                value: value,
                disabled: disabled,
                class: {
                    disabled: disabled,
                }
            }
        },
        digitSetDisabled(digit, disabled) {
            digit.disabled = disabled
            digit.class.disabled = disabled
            return digit
        },
        digitIsDisabled(digit) {
            return digit.disabled;
        },
        digitListRestrictByMax(digitList, number, maxNumber) {
            digitList.forEach(digit => this.digitSetDisabled(digit, '' + number === '0' || parseInt('' + number + digit.value) > parseInt(maxNumber)))
        },
    }
}